.badge-card-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding-bottom: 1rem;

  .badge {
    background: #ffffff;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.4);
    border-radius: 10px 10px 10px 10px;
    overflow: hidden;
    padding: 0;
    height: 400px;
    width: 345px;

    .badge_header {
      height: 160px;
      background: #1b1b25;
      display: flex;
      justify-content: center;

      .logo {
        height: 100%;
        width: 100%;
        object-fit: cover;
        user-select: none;
      }
    }

    .badge_section-name {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 1rem 0;

      .badge_avatar {
        border-radius: 50%;
        margin-right: 1rem;
        width: 80px;
        height: 80px;
        user-select: none;
        object-fit: cover;
      }
    }

    .badge_section-info {
      display: flex;
      flex-direction: column;
      padding: 0.5rem 0;
      background: #f4f4f7;

      .info-group {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;

        .info-secondary-font {
          margin-right: 8px;
          margin-bottom: 0;
          font-size: 16px;
        }

        .badge-font {
          margin-left: 8px;
          font-size: 14px;
          line-height: 22px;
        }
      }
    }

    .badge_footer {
      height: 54px;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #98ca3f;
      font-weight: bold;
      font-style: italic;
    }
  }
}